import React from 'react';
import './Card.css';

const base = 'https://raw.githubusercontent.com/irinainina/rslang/master/data/';

const renderItems = (num, arr) => {

  return arr.filter((item, index) => index >= num * 20 && index < num * 20 + 20)
    .map(item => {
    const { word, image, audio, audioMeaning, audioExample, textMeaning, textExample, transcription} = item;
    return (
      <div className="card" key={word}>
        <h2>{word}</h2>
        <p>{transcription}</p>
        <audio controls src={`${base}${audio}`} />
        <img src={`${base}${image}`} alt={word} />      
        <p dangerouslySetInnerHTML={{__html: textMeaning}} />
        <audio controls src={`${base}${audioMeaning}`} />
        <p dangerouslySetInnerHTML={{__html: textExample}} />      
        <audio controls src={`${base}${audioExample}`} />
      </div>
    );
  });
}

function Card({num, source}) {
  const items = renderItems(num, source);
  return (
    <>
      {items}
    </>
  );
}

export default Card;
